import {Field, Form, Formik, FormikValues} from "formik";
import emailjs from '@emailjs/browser';
import * as Yup from 'yup';
import {useState} from "react";

const ContactForm = () => {
  const [isSending, setIsSending] = useState<boolean>(false);
  const [hasBeenSent, setHasBeenSent] = useState<boolean>(false);
  const [errorMessage, setErrorMessage] = useState<string | undefined>(undefined);

  const errorStyle = 'text-red-600';
  const buttonIsSendingStyle = isSending ? 'opacity-50 cursor-not-allowed' : '';

  const handleSubmitForm = (values: FormikValues) => {
    if (isSending) {
      return;
    }

    if (hasBeenSent) {
      setErrorMessage('Hej! Wysae ju do mnie wiadomo :)');
      return;
    }

    setIsSending(true);

    emailjs.send('www-project-1', 'template_szz1ibe', values, 'e9G_Dv7sS51F-i-1T')
      .then(() => {
        setHasBeenSent(true);
        setIsSending(false);
      }).catch(() => {
        setErrorMessage('Wiadomo nie zostaa wysana! Sprbuj ponownie pniej.');
        setIsSending(false);
    });
  }

  let FormSchema = Yup.object().shape({
    name: Yup.string()
      .min(2, 'Podane imi jest za krtkie!')
      .max(40, 'Podane imi jest za dlugie!')
      .required('Wymagane pole.'),
    surname: Yup.string()
      .min(2, 'Podane nazwisko jest za krtkie!')
      .max(60, 'Podane nazwisko jest za dlugie!')
      .required('Wymagane pole.'),
    email: Yup.string()
      .email('Podany email jest nieprawidowy!')
      .required('Wymagane pole.'),
    description: Yup.string()
      .min(1, 'Tre nie zostaa podana!')
      .max(1000, 'Podana tre jest za duga! Postaraj si streci.')
      .required('Wymagane pole.')
  });

  return (
    <Formik
      initialValues={{name: "", surname: "", email: "", description: "" }}
      validationSchema={FormSchema}
      onSubmit={async (values) => handleSubmitForm(values)}>
      {({ errors, touched }) => (
        <Form className='flex flex-col items-center justify-around h-[30em] md:w-[40em] border p-[1.25em] rounded-lg'>
          <div className='flex flex-col'>
            <label htmlFor="name">Imi</label>
            <Field name='name' type='text' className='w-[20em] h-[2em]' />
            {errors.name && touched.name ? (
              <p className={errorStyle}>{errors.name}</p>
            ) : null}
          </div>

          <div className='flex flex-col'>
            <label htmlFor="lastName">Nazwisko</label>
            <Field name='surname' type='text' className='w-[20em] h-[2em]' />
            {errors.surname && touched.surname ? (
              <p className={errorStyle}>{errors.surname}</p>
            ) : null}
          </div>

          <div className='flex flex-col'>
            <label htmlFor="lastName">Email</label>
            <Field name='email' type='text' className='w-[20em] h-[2em]' />
            {errors.email && touched.email ? (
              <p className={errorStyle}>{errors.email}</p>
            ) : null}
          </div>

          <div className='flex flex-col'>
            <label htmlFor="lastName">W czym mog pomc?</label>
            <Field as='textarea' name='description' type='text' className='w-[20em] h-[5em]' />
            {errors.description && touched.description ? (
              <p className={errorStyle}>{errors.description}</p>
            ) : null}
          </div>

          <button
            type="submit"
            className={`border border-dashed bg-indigo-100 py-[0.5em] px-[1em] ${buttonIsSendingStyle}`}>
            Wylij mi maila!
          </button>

          {isSending && <p className={errorStyle}>Trwa wysyanie maila...</p>}
          {hasBeenSent && !errorMessage && <p className={errorStyle}>Wiadomo zostaa wysana :)</p>}
          {errorMessage && <p className={errorStyle}>{errorMessage}</p>}
        </Form>
      )}
        </Formik>
  );
}

export default ContactForm;